"use client";
import * as React from "react";
import { Slot } from "@radix-ui/react-slot";
import { cn } from "@/lib/utils";

export interface TrialButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  asChild?: boolean;
}

const baseClasses =
  "shiny-button inline-flex items-center justify-center rounded-[11px] min-w-[132px] px-9 py-4 text-base font-bold text-white bg-gradient-to-r from-primary via-accent to-blue-400 shadow-lg shadow-primary/30 transition-opacity hover:opacity-90";

const TrialButton = React.forwardRef<HTMLButtonElement, TrialButtonProps>(
  ({ className, asChild = false, ...props }, ref) => {
    const Comp = asChild ? Slot : "button";
    return (
      <Comp
        className={cn(baseClasses, className)}
        ref={ref}
        {...props}
      />
    );
  }
);
TrialButton.displayName = "TrialButton";

export { TrialButton };
